<template>
    <header>
        <h1>
            {{title}} - {{$route.name}}
        </h1>
        <Button v-show="homePage" @btn-click="$emit('toggle-add-task')" 
                :text="showAddTask? 'Close' : 'Add Task'" 
                :color="showAddTask ? 'red' : 'green'"/>

    </header>
</template>

<script>
    import Button from './Button'

    export default {
        name: 'Header',
        props: {
            title: String,
            showAddTask: Boolean,
        },
        components: {
            Button
        },
        computed: { // Show add task only in Home page based on the router path            
            homePage () {
                if (this.$route.name == 'Home') {
                    return true
                } else {
                    return false
                }
            }
        }
    }
</script>

<style scoped>
    header {
        display:flex;
        justify-content: space-between;
        align-items:center;
        margin-bottom:20px;
    }
</style>